import * as React from "react";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import SearchIcon from "@mui/icons-material/Search";
import CloseIcon from "@mui/icons-material/Close";
import "./SymbolList.css";

export default function SymbolList(props) {
  const [name, setName] = React.useState("");

  React.useEffect(() => {
    setName("");
  }, [props.opened]);

  const handleClick = (e, symbol) => {
    setName(e.target.innerText);
    props.inputVal(symbol);
    props.handleClose();
  };
  const handleChange = (e) => {
    setName(e.target.value);
  };

  return (
    <div>
      <Dialog
        maxWidth="lg"
        open={props.opened}
        onClose={props.handleClose}
        PaperProps={{
          style: {
            backgroundColor: "#181627",
            width: "500px",
            height: "500px",
            borderRadius: "20px",
          },
        }}
      >
        <DialogTitle sx={{ marginLeft: "auto" }}>
          <CloseIcon
            sx={{
              color: "#fff",
              "&:hover": {
                cursor: "pointer",
              },
            }}
            onClick={props.handleClose}
          />
        </DialogTitle>
        <DialogContent style={{}}>
          <DialogContentText>
            <div className="symbol_dialog">
              <SearchIcon
                sx={{
                  color: "#fff",
                  position: "relative",
                  top: "45px",
                  left: "10px",
                }}
              />
              <input
                type="text"
                onChange={handleChange}
                value={name}
                className="symbol_input"
                placeholder="Search Coins"
              />
              <ul>
                {props.symbols
                  .filter((item) => item.item.includes(name.toUpperCase()))
                  .map((symbol) => (
                    <li
                      onClick={(e) => handleClick(e, symbol)}
                      key={symbol.item}
                    >
                      <img src={symbol.image_url} />
                      {symbol.item}
                    </li>
                  ))}
              </ul>
            </div>
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </div>
  );
}
