import React from "react";
import "./Tokenbox.css";
import SymbolList from  './SymbolList'
import ETHBTC from '../Images/ethbtc.png'

function Tokenbox(props) {

  const [open,setOpen] = React.useState(false)
  const [inputVal,setInputVal] = React.useState(null)
  const [formData, setFormData] = React.useState({currprice:0,investPrice:'',shareNumber:0})
  const [currprice, setCurrprice] = React.useState(0);

  const prevCurrprice = React.useRef('')

  // let price = null;
  // let style = {}
  React.useEffect(() => {
    const ws = new WebSocket(`wss://stream.binance.com:9443/ws/${inputVal && inputVal.item.toLowerCase()}@ticker`);
    ws.onmessage = (event) => {
      const response = JSON.parse(event.data);
      console.log(response)
      setCurrprice(response.c);
    };
    return () => {
      setFormData('')
      ws.close();
    };

  }, [inputVal && inputVal.item]);


  let bitCoin = 2363926;

 const calculateShare = (buyprice) => {
  let e = parseFloat(currprice*bitCoin);
  let share = buyprice/e;
  share = parseFloat(share).toFixed(2)
  setFormData(prev=>{
    return {
      ...prev,shareNumber:share
    }
  })
 }

 

React.useEffect(()=>{

prevCurrprice.current = currprice;

 },[currprice])

  const handleClose = () => {
    setOpen(false);
  };

 const backgroundColor = prevCurrprice.current < currprice ? 'rgba(98, 126, 234, 1)' : "red"
 const style = { color: backgroundColor}

 const handleClick = () => {
    setOpen(true)
  }

  const handleChange = (e) => {
    const {name, value} = e.target
    setFormData((prev)=>{
      return {
        ...prev,[name]:value
      }
    })
    if(name == 'investPrice'){
        calculateShare(e.target.value)
    }
  }



  return (
    <div className = "token">
      <div className = "img_container">
        <img src={inputVal && inputVal.image_url} alt="" />
      </div>
      <div id="curve"></div>
      <form action="" className="token_form">
            <label htmlFor="">Current Value <span style={style}>₹{currprice&&parseFloat(currprice*bitCoin).toFixed(2)}</span></label>
            <input type="text" onClick={handleClick} value = {inputVal && inputVal.item}/>
            <label htmlFor="">Amount You want to Invest</label>
            <input type="number"  onChange = {handleChange} name = "investPrice" value = {formData.investPrice} />
            <label htmlFor="">Estimate Number of {inputVal && inputVal.item } you will get</label>
            <input type="text" value = {formData.shareNumber} disabled={true} name = "shareNumber" />
            <button>Buy</button>
      </form>
      <SymbolList opened = {open} handleClose = {handleClose} symbols={props.symbols} inputVal = {setInputVal}/>

    </div>
  );
}

export default Tokenbox;
