import "./App.css";
import React, { useState, useEffect } from "react";
import Navbar from "./Components/Navbar";
import Tokenbox from "./Components/Tokenbox";
import ETHBTC from "./Images/ethbtc.png";
import MATIC from "./Images/matic.png";
import BNBUSDT from "./Images/bnbusdt.png";
import XRPUSDT from "./Images/xrp.png";
import BNBBTC from "./Images/bnb.png";
import SOLUSDT from "./Images/sol.png";
import NEOBTC from "./Images/neo.png";
import QTUMETH from "./Images/qtum.png";
import EOSETH from "./Images/eos.png";
import SNTETH from "./Images/snt.png";
import BNTETH from "./Images/bnt.png";
import BCCBTC from "./Images/bcc.png";
import ETHUSDT from "./Images/eth.png";
import HSRBTC from "./Images/hsr.png";
import ADXBTC from "./Images/adx.png";
import ADABTC from "./Images/ada.png";
import BCHBTC from "./Images/bch.png";
import AMBBTC from "./Images/amb.png";
import BELBTC from "./Images/bela.png";
import AAVEBTC from "./Images/aave.png";

function App() {
  const [symbols, setSymbols] = useState([]);
  let symbolArray = [
    "ETHBTC",
    "MATICUSDT",
    "BNBUSDT",
    "XRPUSDT",
    "BNBBTC",
    "SOLUSDT",
    "NEOBTC",
    "QTUMETH",
    "EOSETH",
    "SNTETH",
    "BNTETH",
    "BCCBTC",
    "ETHUSDT",
    "HSRBTC",
    "ADXBTC",
    "ADABTC",
    "BCHBTC",
    "AMBBTC",
    "BELBTC",
    "AAVEBTC",
  ];

  let img = [
    ETHBTC,
    MATIC,
    BNBUSDT,
    XRPUSDT,
    BNBBTC,
    SOLUSDT,
    NEOBTC,
    QTUMETH,
    EOSETH,
    SNTETH,
    BNTETH,
    BCCBTC,
    ETHUSDT,
    HSRBTC,
    ADXBTC,
    ADABTC,
    BCHBTC,
    AMBBTC,
    BELBTC,
    AAVEBTC,
  ];

  let convertedArray = JSON.stringify(symbolArray);

  useEffect(() => {
    const fetchSymbols = async () => {
      const response = await fetch(
        `https://api.binance.com/api/v3/exchangeInfo?symbols=${convertedArray}`
      );
      const data = await response.json();
      console.log(data);
      const symbols = data.symbols.map((symbol) => symbol.symbol);
      setSymbols(symbols);
    };

    fetchSymbols();
  }, []);

  let newArray = symbols.map((item, id) => ({ item, image_url: img[id] }));

  return (
    <div className="App">
      <Navbar />
      <Tokenbox symbols={newArray} />
    </div>
  );
}

export default App;
